article{
    header{
        div:first-child{
          display: flex;
          justify-content: space-between;
          align-items: center;
            a.post-title{
                font-size: $blog-title-fonts;
                color: $blog-title-color;
                flex: 1;
                font-weight: 800;
            }
            a.post-category{
                background: $blog-categ-bg;
                padding: 0.2em;
                color: $blog-categ-color;
                font-weight: 800;
            }
        }
        div:nth-child(2){
            margin-bottom: 0.4rem;
            a.post-y{
              color: $blog-year-color;
            }
            a.post-m{
              color: $blog-month-color;
            }
            a.post-d{
              color: $blog-day-color;
            }
        }
        div:nth-child(3){
            a{
                margin-right: 1rem;
                color: $blog-tag-color;
            }
            margin-bottom: 1rem;
        }
    }  
    >div.post-content{
    text-align: justify;
    word-wrap:break-word; 
    line-height: 1.5rem;
    }
    footer.post-more{
        margin-left: 0;
        margin-right: 0;
        margin-bottom: 5rem;
        a{
            background: $blog-more-bg;
            display: inline-block;
            padding: 0.4em 0.8em;
            color: $blog-more-color;
            font-weight: 800;
        }
        hr {
            border: 0;
            padding-top: 1px;
            background: linear-gradient(135deg, transparent,#d0d0d5);
          }
    }
}

div.pagination{
  font-weight: 800;
    text-align: center;
    position: relative;
    margin-top: 4rem;
    margin-bottom: 0;
    padding: {
      top: 1.5rem;
      bottom: 1.5rem;
    };
    border-top: solid 1px #c4cbcf;
    border-bottom: solid 1px #c4cbcf;

    a {
      text-decoration: none;
      color: $blog-pagination-color;
      &.prev {
        position: absolute;
        left: 0;
      }

      &.next {
        position: absolute;
        right: 0;
      }

      &[href*=archive] {
        &:before, &:after {
          content: "â€”";
          padding: 0 0.3rem;
        }
      }
    }
  }
