.nav-box {
  display: flex;
  flex-wrap: nowrap;
  line-height: $catoliver-font-s * 2;
  align-items: center;
  justify-content: space-between;

  > li:first-child {
    font-size: $catoliver-font-s;
    font-family: $catoliver-font-f !important;
    ul{
      li{
        a{
          color: $catoliver-color !important;
        }
      }
    }

  }

  .nav-main{
    display: flex;
    font-family: $nav-font-base;
    font-weight: bold;
    > li:nth-child(1) {
      
      > ul {
        display: flex;
        flex-wrap: nowrap;
        
        li {
          margin-left: 1rem;
          margin-right: 1rem;
          a{
            color:$nav-color-base;
          }
        }
      }
    }
  
    > li:nth-child(2) {
      > div {
        display: none;
      }
      > ul {
        display: flex;
        flex-wrap: nowrap;
        justify-content: flex-end;
        li {
          margin-left: 1rem;
        }
        }
      }
    }

  }



@media only screen and (max-width: 640px) {

  .nav-box {
    display: flex;
    flex-direction: column;
    white-space: nowrap;
    > li:first-child {
      text-align: center;
    }
    > li:nth-child(2) {
      > ul {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        li {
          margin-left: 0;
        }
      }
    }
    > li:nth-child(3) {
      line-height: normal;
      display: flex;
      > div {
        display: block;
        flex: 1;
        padding: 1rem 0 1rem 0;
        hr {
          border: 0;
          padding-top: 1px;
          background: linear-gradient(135deg, #d0d0d5, transparent);
        }
      }
    }

  }
}


