div.tag-archive{
    margin-bottom: 1.5rem;
    margin-top: 1rem;
    div.tags-name{
        display: inline-block;
        margin-right: 1rem;
        margin-bottom: 1rem;
        a{
            color: $archive-tag-color;
        }
        .tags-num{
            font-size: 0.6rem;
        }
    }
}

.tag-title{
    font-weight: 800;
}

div.archive-show{
    display: flex;
    flex-direction: column;
    justify-content: center;
    #archive-c{
        display: flex;
        justify-content: flex-start;
        span{
            color: $archive-sort-color;
            font-weight: 800;
        }
        margin-bottom: 1rem;
    }


    #time-archive{
        display: normal;
        font-weight: 800;
        .time-a-box{
            display: flex;
            align-items: center;
            justify-content:  space-between;
            margin-top: 1rem;
        }
        .time-year{
            color: $archive-year-color;
        }
        .time-title{

            display: flex;
            justify-content: space-between;
            align-items: center;
            a{
                color: $archive-title-color;
            }
            span{
                background: $archive-categT-bg;
                padding: 0.4em;
                color: $archive-categT-color;
                margin-left: 0.2em;
            }
        }
        hr{
            border: 1px dotted ;
        }
    }

}



ul.posts{
    font-weight: 800;
    li{
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 0.5rem;
        margin-bottom: 0.5rem;
        a{
            color: $archive-title-color;
            flex: 1;
        }
        span{
            background: #e5e5e5;
            padding: 0.4em;
            color: #6f6f6f;
        }
    }

}