div.project{
    article{
        margin-bottom: 5rem;
        header{
            display: flex;
            justify-content: space-between;
            align-items: center;
            h1{
                font-size: $project-title-fonts;
                color: $project-title-color;
            }
            h2{
                background: $project-year-bg;
                padding: 0.4em;
                color: $project-year-color;
                margin-left: 0.2em;
            }
        }
        table{
            margin-top: 2rem;
            margin-top: 2rem;
        }
        p{
            text-align: justify;
            word-wrap:break-word; 
            line-height: 2rem;
        }
    }
}