$max-width: 960px !default;

$mar-nar: 10px;
$mar-min: 18px;
$mar-nor: 25px;
$mar-med: 35px;
$mar-wid: 55px;
$mar-big: 65px;

// $bg-color: white;
// $font-base: Consolas, Monaco, Menlo, "DejaVu Sans Mono", "Bitstream Vera Sans Mono", "Courier New", "Lucida Console", "Lucida Sans Typewriter", "Liberation Mono", "Nimbus Mono L", Monaco, Courier, monospace;
$font-base: "PT serif", "Microsoft Yahei";

// navigation
$catoliver-font-s: 2.5rem;
$catoliver-color: black;
$catoliver-font-f: "Lucida Console",  $font-base;
$nav-color-base: black;
$nav-font-base:   $font-base;

//blog
$blog-title-color: #2f2f35;
$blog-title-fonts: 1rem;

$blog-categ-bg: #e5e5e5;
$blog-categ-fonts: 1rem;
$blog-categ-color: #6f6f6f;

$blog-year-color: #BBB;
$blog-year-fonts: 1rem;

$blog-month-color: #BBB;
$blog-year-fonts: 1rem;

$blog-day-color: #BBB;
$blog-year-fonts: 1rem;

$blog-tag-color: #1781b5;
$blog-tag-fonts: 1rem;

$blog-more-color: #6f6f6f;
$blog-more-fonts: 1.2rem;
$blog-more-bg: #e5e5e5;

$blog-pagination-color: #158bb8;
$blog-pagination-fonts: 1rem;

//archive
$archive-tag-color: #1781b5;
$archive-tag-fonts: 1.2rem;

$archive-sort-color: #2f2f35;
$archive-sort-fonts: 1rem;
$archive-sort-fontf: $font-base, "Times New Roman", "SimSun";

$archive-year-color: #2f2f35;
$archive-year-fonts: 2rem;

$archive-categ-color: #2f2f35;
$archive-categ-fonts: 2rem;

$archive-title-color: #6f6f6f;
$archive-title-fonts: 1.2rem;

$archive-categT-color: #6f6f6f;
$archive-categT-bg: #e5e5e5;

$archive-yearT-color: #6f6f6f;
$archive-yearT-bg: #e5e5e5;

//project
$project-title-color: #444;
$project-title-fonts: 2rem;

$project-year-color: #6f6f6f;
$project-year-bg: #e5e5e5;

//search
$search-title-color: #777;
$search-title-fonts: 2rem;

$search-date-color: #777;
$search-date-fonts: 2rem;

//footer
$footer-color: #777;
$footer-fonts: 2rem;

html {
  font-size: 16px;
}

@media only screen and (min-width: 320px) {
  body {
    font-size: 16px;
  }
}

@media only screen and (min-width: 480px) {
  body {
    font-size: 18px;
  }
}

@media only screen and (min-width: 768px) {
  body {
    font-size: 20px;
  }
}

@media only screen and (min-width: 992px) {
  body {
    font-size: 21px;
  }
}

@media only screen and (min-width: 1024px) {
  body {
    font-size: 22px;
  }
}

ul {
  border: 0;
  li {
    list-style-image: none;
    list-style-type: none;
  }
}

.b-hr {
  border: 0;
  padding-top: 1px;
  background: linear-gradient(to right, transparent, #d0d0d5, transparent);
}

html > body {
  // background-color: $bg-color;
  max-width: $max-width;
  -webkit-text-size-adjust: none;
  text-size-adjust: 100%;
  font-family: $font-base;
  margin: 0 auto;
  overflow-y: scroll;

  nav,
  section,
  footer {
    margin-left: $mar-nar;
    margin-right: $mar-nar;

    @media only screen and (min-width: 320px) {
      margin-left: $mar-min;
      margin-right: $mar-min;
    }

    @media only screen and (min-width: 480px) {
      margin-left: $mar-nor;
      margin-right: $mar-nor;
    }

    @media only screen and (min-width: 768px) {
      margin-left: $mar-med;
      margin-right: $mar-med;
    }

    @media only screen and (min-width: 992px) {
      margin-left: $mar-wid;
      margin-right: $mar-wid;
    }

    @media only screen and (min-width: 1024px) {
      margin-left: $mar-big;
      margin-right: $mar-big;
    }
  }

  section {
    flex: 1;
    margin-top: 2.5rem;
    margin-bottom: 2.5rem;
  }
}